import { Injectable } from '@angular/core';
import { Api } from 'core/services/api.service';

export class Comment {

  user_id: string;
  name: string;
  comment: string;
  commented_at: string;
  is_review: boolean;
}

export class Feedback {

  _id?: string;
  feedback: string;
  user_id?: string;
  is_anonymous?: boolean;
  name?: string;
  comments?: Comment[];
  feedback_status?: number;
}

@Injectable()
export class FeedbackService extends Api {

  private _base_url = '/employees/feedbacks';

  postFeedback(feedback: Feedback): Promise<Object> {

    return this.post(this._base_url, feedback)
      .then(response => response);
  }

  getFeedbacks(query?: Map<string, any>): Promise<Feedback[]> {

    return this.get(this._base_url, query)
      .then(response => response['data'] as Feedback[]);
  }

  getFeedback(feedback_id: string): Promise<Feedback> {

    return this.get(this._base_url + '/' + feedback_id)
      .then(response => response);
  }

  submitComment(feedback_id, comment): Promise<Object> {

    return this.put(this._base_url + '/comment/' + feedback_id, { comment: comment })
      .then(response => response);
  }

  getReviewFeedbacks(query?: Map<string, any>): Promise<Feedback[]> {

    return this.get(this._base_url + '?review=true', query)
      .then(response => response['data'] as Feedback[]);
  }

  getReviewFeedback(feedback_id: string): Promise<Feedback> {

    return this.get(this._base_url + '/' + feedback_id + '?review=true')
      .then(response => response);
  }

  submitReviewComment(feedback_id, comment): Promise<Object> {

    return this.put(this._base_url + '/comment/' + feedback_id + '?review=true', { comment: comment })
      .then(response => response);
  }
}
