import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthenticationGuard } from 'core/services/authentication.service';
import { AuthorizationGuard } from 'core/services/authorization.service';
import { ListFeedbackComponent } from './list-feedback/list-feedback.component';
import { AddFeedbackComponent } from './add-feedback/add-feedback.component';
import { ReviewFeedbackComponent } from './review-feedback/review-feedback.component';

const routes: Routes = [
  {
    path: '',
    data: {
      title: 'Feedback'
    },
    children: [
      {
        path: '',
        redirectTo: 'list'
      },
      {
        path: 'list',
        canActivate: [AuthenticationGuard, AuthorizationGuard],
        data: {
          title: 'List',
          access: {
            module: 'employee',
            entity: 'feedback',
            action: 'find'
          }
        },
        component: ListFeedbackComponent
      },
      {
        path: 'add',
        canActivate: [AuthenticationGuard, AuthorizationGuard],
        data: {
          title: 'Add',
          access: {
            module: 'employee',
            entity: 'feedback',
            action: 'create'
          }
        },
        component: AddFeedbackComponent
      },
      {
        path: 'review',
        canActivate: [AuthenticationGuard, AuthenticationGuard],
        data: {
          title: 'Review',
          access: {
            module: 'employee',
            entity: 'feedback',
            action: 'find-review'
          }
        },
        component: ReviewFeedbackComponent
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class FeedbackRoutingModule { }
