import { Component, OnInit, ViewChild } from '@angular/core';

import { Feedback, FeedbackService } from 'modules/employee/feedback/feedback.service';
import { ErrorModalComponent } from 'shared/components/error-modal/error-modal.component';
import { User } from 'core/providers/user.service';
import { ExaTask } from '../../../../core/providers/exatask.service';

@Component({
  selector: 'app-review-feedback',
  templateUrl: './review-feedback.component.html',
  providers: [FeedbackService]
})
export class ReviewFeedbackComponent implements OnInit {
  @ViewChild('errorModal') errorModal: ErrorModalComponent;

  feedbacks: Feedback[] = [];
  feedback_details: Feedback;

  comment: string;
  error: string;

  commentOpened: boolean = false;

  constructor(
    private feedbackService: FeedbackService,
    private user: User,
    public exatask: ExaTask
  ) {
    this.exatask.setTitle('Employee Feedback Review List');
  }

  async ngOnInit() {

    this.loadFeedbacks();
  }

  loadFeedbacks() {

    const self = this;
    this.feedbackService.getReviewFeedbacks().then(feedbacks => {

      self.feedbacks = feedbacks;
    });
  }

  openFeedback(feedback_id) {

    const self = this;
    this.feedbackService.getReviewFeedback(feedback_id).then(feedback => {

      self.commentOpened = true;
      self.feedback_details = feedback;
    });
  }

  closeFeedback() {

    this.commentOpened = false;
    this.feedback_details = undefined;
  }

  submitComment() {

    const feedback_id = this.feedback_details._id;
    this.feedbackService.submitReviewComment(feedback_id, this.comment).then(data => {

      const app_user = this.user;
      this.feedback_details.comments.push({
        user_id: app_user.user_id,
        name: app_user.name,
        comment: this.comment,
        commented_at: (new Date()).toString(),
        is_review: true
      });
      this.comment = '';

    }).catch(err => {

      this.error = err.error.message.text;
      this.errorModal.show();
    });
  }
}
