import { Component, OnInit, ViewChild } from '@angular/core';

import { Feedback, FeedbackService } from 'modules/employee/feedback/feedback.service';
import { ErrorModalComponent } from 'shared/components/error-modal/error-modal.component';
import { Access } from 'core/providers/access.service';
import { ExaTask } from '../../../../core/providers/exatask.service';

@Component({
  selector: 'app-list-feedback',
  templateUrl: './list-feedback.component.html',
  providers: [FeedbackService]
})
export class ListFeedbackComponent implements OnInit {
  @ViewChild('errorModal') errorModal: ErrorModalComponent;

  feedbacks: Feedback[] = [];
  feedback_details: Feedback;

  comment: string;
  error: string;

  reviewAllowed: boolean = false;
  commentOpened: boolean = false;

  constructor(
    private feedbackService: FeedbackService,
    private access: Access,
    public exatask: ExaTask
  ) {
    this.exatask.setTitle('Employee Feedback List');
  }

  async ngOnInit() {

    this.loadFeedbacks();

    this.reviewAllowed = await this.access.hasAccess({
      module: 'employee',
      entity: 'feedback',
      action: 'review'
    });
  }

  loadFeedbacks() {

    const self = this;
    this.feedbackService.getFeedbacks().then(feedbacks => {

      self.feedbacks = feedbacks;
    });
  }

  openFeedback(feedback_id) {

    const self = this;
    this.feedbackService.getFeedback(feedback_id).then(feedback => {

      self.commentOpened = true;
      self.feedback_details = feedback;
    });
  }

  closeFeedback() {

    this.commentOpened = false;
    this.feedback_details = undefined;
  }

  submitComment() {

    const feedback_id = this.feedback_details._id;
    this.feedbackService.submitComment(feedback_id, this.comment).then(data => {

      this.feedback_details.comments.push({
        user_id: this.feedback_details.user_id,
        name: this.feedback_details.name,
        comment: this.comment,
        commented_at: (new Date()).toString(),
        is_review: false
      });
      this.comment = '';

    }).catch(err => {

      this.error = err.error.message.text;
      this.errorModal.show();
    });
  }
}
