import { Component, OnInit, ViewChild } from '@angular/core';
import { FeedbackService, Feedback } from 'modules/employee/feedback/feedback.service';
import { ErrorModalComponent } from 'shared/components/error-modal/error-modal.component';
import { SuccessModalComponent } from 'shared/components/success-modal/success-modal.component';
import { ExaTask } from '../../../../core/providers/exatask.service';

const error_map = {
  feedback: 'Feedback'
};

@Component({
  selector: 'app-add-feedback',
  templateUrl: './add-feedback.component.html',
  providers: [FeedbackService]
})
export class AddFeedbackComponent implements OnInit {
  @ViewChild('errorModal') errorModal: ErrorModalComponent;
  @ViewChild('successModal') successModal: SuccessModalComponent;

  feedback: Feedback;

  error: string;
  invalid_attributes: string[];

  constructor(
    private feedbackService: FeedbackService,
    public exatask: ExaTask
  ) {

    this.feedback = new Feedback();
    this.exatask.setTitle('Add Employee Feedback');
  }

  async ngOnInit() {
  }

  private __validate() {
    return true;
  }

  postFeedback() {

    if (this.__validate()) {

      const feedback = JSON.parse(JSON.stringify(this.feedback));
      this.error = '';
      this.invalid_attributes = [];

      this.feedbackService.postFeedback(feedback).then(data => {

        this.successModal.show('/employee/feedback');

      }).catch(err => {

        this.error = err.error.message.text;
        if (err.error.invalid_attributes) {
          for (const key in err.error.invalid_attributes) {
            if (err.error.invalid_attributes.hasOwnProperty(key)) {
              this.invalid_attributes.push(error_map[key] ? error_map[key] : err.error.invalid_attributes[key]);
            }
          }
        }
        this.errorModal.show();
      });
    }
  }
}
